@import '../../scss/variable.scss';

.footer {
  z-index: $z-layout;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: $ft-s-small;
  display: flex;
  justify-content: space-between;
  padding-top: 0.4rem;
  padding-bottom: 0.5rem;
  padding-left: 15px;
  padding-right: 15px;
  span {
    color: var(--text-color) !important;
  }
  svg {
    stroke: var(--text-color);
    margin-bottom: 1px;
    width: $ft-s-small;
  }
  a {
    text-decoration: none;
  }
  .copy-right {
    text-align: left;
    padding-left: 12px;
    &::before {
      content: '©';
      float: left;
      margin-left: -13px;
      margin-top: 4px;
    }
  }
  .toggle-box {
    position: relative;
    top: 1px;
    min-width: 110px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .lang-toggle-box {
    margin-bottom: 0.2rem;
    .lang-toggle {
      margin: 0 5px;
      cursor: pointer;
    }
    .active {
      border-width: $b-w;
      border-bottom: $b-w solid var(--text-color);
    }
  }
  .dividing-line {
    margin: 0 5px;
  }
  .dark-toggle-box {
    .dark-toggle {
      margin: 0 5px;
      cursor: pointer;
    }
    .dark-active {
      border-bottom: $b-w solid var(--text-color);
    }
  }
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
  .footer {
    padding-left: 11px;
    padding-right: 11px;
  }
}

